<template>
  <div class="setting-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-row>
        <el-col :span="12">
          <el-form
            ref="userProfile"
            :model="userProfile"
            label-width="60px"
            :rules="profileRules"
          >
            <el-form-item label="">
              <el-input disabled v-model="userProfile.id"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input disabled v-model="userProfile.mobile"></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input v-model="userProfile.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="userProfile.email"></el-input>
            </el-form-item>
            <el-form-item label="" prop="intro">
              <el-input type="textarea" v-model="userProfile.intro"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                :loading="buttonLoading"
                @click="onSaveProfile"
              ></el-button>
              <el-button
                @click="onReset"
                :loading="buttonLoading"
              ></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12" style="display:flex;justify-content:center;">
          <div class="avatar-wrapper">
            <el-avatar
              :size="240"
              :src="userProfile.photo"
            ></el-avatar>
            <el-button
              class="edit-button"
              icon="el-icon-edit"
              type="primary"
              size="mini"
              circle
              @click="$refs.file.click()"
            ></el-button>
            <input
              type="file"
              id="file"
              @change="onFileChange"
              ref="file"
              hidden
            >
          </div>
        </el-col>
      </el-row>
    </el-card>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :append-to-body="true"
      @opened="onDialogOpened"
      @closed="onDialogClosed"
      :before-close="onBeforeClose"
    >
      <div class="image-wrapper">
        <img
          class="image"
          :src="previewURL"
          ref="preview-image"
        >
      </div>
      <el-button
        style="margin-top:20px;width:100%;"
        type="primary"
        @click="onUpdateAvatar"
        :loading="isLoading"
      ></el-button>
    </el-dialog>
  </div>
</template>

<script>
import {
  getUserProfile,
  uploadAvatar,
  updateProfile
} from 'api/user.js'
import EventBus from 'utils/bus.js'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'

export default {
  name: 'SettingIndex',
  data() {
    return {
      userProfile: {}, // 
      dialogVisible: false, // 
      previewURL: '', // 
      cropper: null, // 
      isLoading: false, // 
      profileRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^.{1,40}$/, message: ' 1  40 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      buttonLoading: false //  loading
    }
  },
  created() {
    this.loadUserProfile()
  },
  methods: {
    async loadUserProfile() {
      this.buttonLoading = true
      try {
        const res = await getUserProfile()
        this.userProfile = res.data.data
        this.buttonLoading = false
      } catch (ex) {
        console.log(ex)
        this.buttonLoading = false
      }
    },
    onFileChange() {
      // 
      const file = this.$refs.file
      // 
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewURL = blobData
      // 
      this.dialogVisible = true
      this.$refs.file.value = null
    },
    onDialogOpened() {
      const image = this.$refs['preview-image']

      this.cropper = new Cropper(image, {
        aspectRatio: 1, //  1:1
        viewMode: 1, // 
        cropBoxResizable: false, // 
        dragMode: 'move' // 
      })
    },
    onDialogClosed() {
      // 
      this.cropper.destroy()
    },
    async onBeforeClose(done) {
      try {
        await this.$warningBox('', '')
        done()
      } catch {}
    },
    onUpdateAvatar() {
      this.isLoading = true // 

      // 1. 
      this.cropper.getCroppedCanvas().toBlob(async file => {
        const fd = new FormData()
        fd.append('photo', file)
        // 2. 
        try {
          //  'Content-Type': 'multipart/form-data'
          //  header  data  FormData 
          // 
          const res = await uploadAvatar(fd)

          EventBus.$emit('avatarUpdated', res.data.data.photo) // 

          // 3. 
          this.dialogVisible = false

          // 4. 
          this.$msgSuccess('')

          // 5.  blob 
          const blobData = window.URL.createObjectURL(file)
          this.userProfile.photo = blobData

          this.isLoading = false // 
        } catch (ex) {
          console.log(ex)

          // 3. 
          this.dialogVisible = false

          // 4. 
          this.$msgError('')

          this.isLoading = false // 
        }
      })
    },
    onSaveProfile() {
      this.$refs.userProfile.validate(async valid => {
        if (!valid) return false

        try {
          // 
          const { name, intro, email } = this.userProfile
          await updateProfile({ name, intro, email })

          // 
          EventBus.$emit('profileUpdated', { name, email })

          // 
          this.loadUserProfile()

          // 
          this.$msgSuccess('')
        } catch (ex) {
          console.log(ex)
          // 
          this.$msgError('')
        }
      })
    },
    onReset() {
      this.loadUserProfile()
      this.$msgSuccess('')
    }
  }
}
</script>

<style lang="scss" scoped>
.image-wrapper {
  .image {
    display: block;
    max-width: 100%;
    height: 200px;
  }
}
.avatar-wrapper {
  position: relative;
  .edit-button {
    position: absolute;
    left: 0;
    bottom: 0;
  }
}
</style>
